___CSS_LOADER_EXPORT___.push([module.id, `:root {
	--form-bg-color: rgb(48, 48, 48);
	--background-color: #00011f;
}

body {
	font-family: "Arial", sans-serif;
	display: grid;
	height: 100vh;
	grid-template-columns: 0.5fr 1fr 1fr 0.5fr;
	grid-template-rows: 1fr 1fr 1fr;
	background-color: var(--background-color);
	position: relative;
}
.grid {
	display: grid;
	grid-template-columns: repeat(10, 40px);
	margin: 0px;
	position: relative;
}
.cell {
	width: 40px;
	height: 40px;
	border: 1px solid var(--background-color);
	text-align: center;
	line-height: 40px;
	cursor: pointer;
}

.cell:hover {
	background-color: rgba(0, 255, 255, 0.404);
}

#gridWrap {
	display: flex;
	justify-content: space-around;
	grid-area: 2/2/2/4;
}

#playerGrid {
	display: grid;
	border: var(--background-color) solid 1px;
}

#computerGrid {
	background-color: var(--form-bg-color);
	border: var(--background-color) solid 1px;
}

#nameDialog {
	visibility: hidden;
	position: absolute;
	display: flex;
	flex-direction: column;
	padding: 10px;
	justify-content: center;
	width: 300px;
	height: 125px;
	top: 320px;
	left: 805px;
	background-color: var(--form-bg-color);
	border-radius: 15px;
	z-index: 5;
	border: var(--background-color) 2px solid;
}

#playerName {
	grid-area: 3/2/3/2;
	text-align: center;
	padding-top: 25px;
}

#compName {
	grid-area: 3/3/3/3;
	text-align: center;
}

#title {
	grid-area: 1/2/1/4;
	text-align: center;
}

#placeShipNotice {
	display: flex;
	justify-content: center;
	grid-area: 1/2/1/2;
	justify-self: center;
	align-self: flex-end;
	background-color: var(--form-bg-color);
	width: 62.7%;
	text-align: center;
	border-top-left-radius: 15px;
	border-top-right-radius: 15px;
	padding-top: 5px;
	visibility: hidden;
	color: white;
	border-top: black solid 1px;
	border-left: black solid 1px;
	border-right: black solid 1px;
}

#vertHorzWrap {
	color: white;
	display: flex;
	grid-area: 3/2/3/2;
	justify-content: center;
	background-color: var(--form-bg-color);
	height: fit-content;
	width: 62.8%;
	justify-self: center;
	border-bottom-left-radius: 15px;
	border-bottom-right-radius: 15px;
	border-bottom: black solid 1px;
	border-left: black solid 1px;
	border-right: black solid 1px;
	visibility: hidden;
}

#vertHorzChoice {
	display: flex;
	justify-content: space-between;
	padding-top: 7px;
	padding-bottom: 7px;
	width: 62.5%;
	z-index: 10;
}

#logo {
	width: 550px;
	grid-area: 1/2/1/4;
	justify-self: center;
	padding-top: 10px;
}

#shipGrid {
	position: absolute;
	height: 100%;
	width: 100%;
	display: grid;
	grid-template-columns: repeat(10, 40px);
	grid-template-rows: repeat(10, 40px);
	z-index: -1;
	background-color: var(--form-bg-color);
}

.vertical {
	rotate: 90deg;
}

#shipGrid img {
	margin: 0px;
}

.battleship {
	position: relative;
	width: 145px;
	height: 37px;
	margin-top: 4px;
	top: 2px;
	left: 3px;
}

.battleship.vertical {
	transform: translateX(-40%) translateY(151%);
}

.carrier {
	position: relative;
	width: 190px;
	height: 37px;
	margin-top: 4px;
	top: 5px;
	left: 5px;
}

.carrier.vertical {
	transform: translateX(-45%) translateY(219%);
}

.destroyer {
	position: relative;
	width: 100px;
	height: 37px;
	margin-top: 4px;
	top: 3px;
	left: 5px;
}

.destroyer.vertical {
	transform: translateX(-45%) translateY(98%);
}

.patrol {
	position: relative;
	width: 65px;
	height: 37px;
	margin-top: 4px;
	top: 3px;
	left: 7px;
}

.patrol.vertical {
	transform: translateX(-35%) translateY(58%);
}

.sub {
	position: relative;
	width: 110px;
	height: 37px;
	margin-top: 4px;
	top: 3px;
	left: 4px;
}

.sub.vertical {
	transform: translateX(-35%) translateY(107%);
}

.shipOverlap {
	filter: invert(12%) sepia(82%) saturate(6560%) hue-rotate(359deg) brightness(103%) contrast(110%);
}
